// server/controllers/uploadController.js
const path = require('path');

exports.uploadFile = async (req, res) => {
  try {
    if (!req.files || Object.keys(req.files).length === 0) {
      return res.status(400).json({ message: 'No files were uploaded' });
    }

    const { file } = req.files;
    const uploadPath = path.join(__dirname, '../uploads', file.name); // Define upload path

    // Move the uploaded file to the defined path
    await file.mv(uploadPath);

    // Respond with success message and file path
    res.status(200).json({ message: 'File uploaded successfully', filePath: uploadPath });
  } catch (error) {
    console.error('Error uploading file:', error);
    res.status(500).json({ message: 'File upload failed', error: error.message });
  }
};
